import axios from 'axios';

export default {
    state: {
        products: [],
    },
    actions: {
        getProducts(context) {
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products`;
            // vm.isLoading = true;
            context.commit('LOADING', true); // vuexisLoadingstore/index.jsundateLoadingloading
            axios.get(url).then((response) => {
                context.commit('PRODUCTS', response.data.products);
                console.log("getProducts", response);
                // vm.isLoading = false;
                context.commit('LOADING', false); // vuexisLoadingstore/index.jsundateLoadingloading
            });
        },
    },
    mutations: {
        // 3. mutationfunction
        // : (state, status)
        // statestatestate.isLoading 
        // statusstatusactionsupdateLoadingstatus
      PRODUCTS(state, payload) {
        state.products = payload;
      },
    },
    getters: {
      products(state) {
        return state.products;
      },
    },
}