import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import 'bootstrap' // VueBootstrapjQuery
// Import component
// Fontawesome component
import { library } from '@fortawesome/fontawesome-svg-core'
import { faSpinner } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { fas } from '@fortawesome/free-solid-svg-icons'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { dom } from '@fortawesome/fontawesome-svg-core'
// Loading overlay component
import Loading from 'vue-loading-overlay'; // 
// Import stylesheet
import 'vue-loading-overlay/dist/vue-loading.css'; // css
import VeeValidate from 'vee-validate'
import zhTW from 'vee-validate/dist/locale/zh_TW'
import VueI18n from 'vue-i18n'

import App from './App.vue'
import router from './router'
import store from './store'
import './bus';
import currencyFilter from './filters/currency'
import orderDate from './filters/date'

// Fontawesome
library.add(faSpinner, fas, fab)
Vue.component('font-awesome-icon', FontAwesomeIcon)
dom.watch()

Vue.use(VueAxios, axios) // VueAxiosAxios
Vue.config.productionTip = false
Vue.use(VueI18n) // vee-validate

const i18n = new VueI18n({
  locale: 'zhTW'
})
Vue.use(VeeValidate, {
  events: 'input|blur', //
  i18n,
  dictionary: {
    zhTW
  }
})

Vue.component('Loading', Loading); // LoadingVue.component(, )
Vue.filter('currency', currencyFilter); // filter
Vue.filter('date', orderDate);

axios.defaults.withCredentials = true;

router.beforeEach((to, from, next) => {
  console.log('to', to, 'from', from, 'next', next);
  // 
  // next()
  // next();
  // 
  // HelloWorldmeta-requireAuth = truetrue
  if (to.meta.requiresAuth) { 
    // /admin/signin api/api/user/check
    const api = `${process.env.VUE_APP_APIPATH}/api/user/check`;
    // this.$httproutervue
    // Vuethis.$httpaxios
    // this.$http.post(api).then((response) => {
    axios.post(api).then((response) => {
      console.log(response.data);
      // iflogin
      if (response.data.success) {
        next(); // 
      } else {
        next('/login'); // 
      }
    })
  } else {
    next();
  }
})

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
