<template>
  <div>
    <!-- formbootstrap -->
    <form class="form-signin" @submit.prevent="signin">
      <h1 class="h3 mb-3 font-weight-normal"></h1>
      <label for="inputEmail" class="sr-only">Email address</label>
      <input type="email" id="inputEmail" class="form-control" placeholder="Email address" v-model="user.username" required autofocus> <!-- v-modelusername -->
      <label for="inputPassword" class="sr-only">Password</label>
      <input type="password" id="inputPassword" class="form-control" placeholder="Password" v-model="user.password" required> <!-- v-modelusername -->
      <div class="checkbox mb-3">
      </div>
      <button class="btn btn-lg btn-dark btn-block" type="submit" @enter="signin" @click.prevent="signin"></button>
      <p class="mt-5 mb-3 text-muted">&copy; 2017-2020</p>
    </form>
        <!-- formbootstrap -->
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
        // 
      user: {
          username: 'cc.paparun@gmail.com',
          password: '',
      }
    };
  },
  methods: {
    signin (){
        const vm = this; // uservm = this
        const api = `${process.env.VUE_APP_APIPATH}/admin/signin`; 
        // 'https://vue-course-api.hexschool.io/signin'
        // /signin/admin/admin/signincookiesVue
        // this.$http.get -> getpost(post)(get from database/api)
        this.$http.post(api, vm.user).then((response) => { 
            console.log(response.data);
            if (response.data.success){ 
                // vm.$router.push('/')  // 
                // ('/admin/products')
                vm.$router.push('/admin/products')
            }
    })
  },
  },
};
</script>

