<template>
  <div>
    <!-- loading-overlaydivdiv -->
    <div class="vld-parent">
      <!-- isLoading = truefalseisLoadingdata()return{} -->
      <loading :active.sync="isLoading"></loading>
    </div>

    <div class="text-right mt-4">
      <!-- data-toggle="modal" data-target="#productModal" modal -->
      <!-- <button class="btn btn-primary" data-toggle="modal" data-target="#productModal"></button> -->
      <!-- modal'openModal()' -->
      <button class="btn btn-primary" @click="openModal(true)"></button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td>{{ item.origin_price | currency }}</td>
          <td>{{ item.price | currency }}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <button
            class="btn btn-outline-primary btn-sm mr-1 mt-2"
            @click="openModal(false, item)"
          ></button>
          <button class="btn btn-outline-danger btn-sm mt-2" @click="openDelModal(item)"></button>
        </tr>
      </tbody>
    </table>
    <!-- v-model -->

    <!--  -->
     <Pagination :pagination="pagination" @changePage="getProducts"></Pagination> 
    <!-- Pagination -->
    <!-- :paginationdatapagination -->
    <!-- @changePagePaginationnewPage(page)this.$emit('changePage',page)getProducts(page)api -->

    <!--  Modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    v-model="tempProduct.imageUrl"
                    placeholder=""
                  />
                </div>
                <div class="form-group">
                  <label for="customFile">
                     
                    <!-- loading(true) -->
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <!-- input@changeuploadFileuploadFile -->
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  />
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  :src="tempProduct.imageUrl"
                  alt
                />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    v-model="tempProduct.title"
                    placeholder=""
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      v-model="tempProduct.category"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="unit" class="form-control" id="unit" v-model="tempProduct.unit" placeholder="" />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      v-model="tempProduct.origin_price"
                      placeholder=""
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      v-model="tempProduct.price"
                      placeholder=""
                    />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    v-model="tempProduct.description"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    v-model="tempProduct.content"
                    placeholder=""
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      v-model="tempProduct.is_enabled"
                      id="is_enabled"
                    />
                    <label class="form-check-label" for="is_enabled"></label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>

    <!--  Modal -->
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempProduct.title }}</strong>
            <span>? </span>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="deleteProduct"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery"; // openModal'$''$'jQueryjQuery
import Pagination from '@/components/Pagination'; // Pagination.vueimport

export default {
  name: "Products",
  data() {
    return {
      products: [],
      pagination: {},
      tempProduct: {},
      isNew: false, // model
      // isLoading: false, // loading
      status: {
        fileUploading: false
      }
    };
  },
  components: { 
    Pagination, // Pagination.vueimport PaginationProducts.vue
  },
  methods: {
    // api
    getProducts(page = 1) { // page = 1ES6
      // API                                                // products?page={page}
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products?page=${page}`;
      const vm = this; // APIdatavmthis
      console.log(process.env.VUE_APP_APIPATH, process.env.VUE_APP_CUSTOMPATH);
      vm.$store.dispatch("updateLoading", true); // apiloading
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.isLoading = false; // loading
        vm.products = response.data.products; // response.dataAPIdataproducts:[]
        vm.pagination = response.data.pagination; // paginationvuepagination
        console.log('response.data.pagination:', response.data);
      });
    },

    // modal
    openModal(isNew, item) {
      if (isNew) {
        this.tempProduct = {}; // this.tempProduct
        this.isNew = true;
      } else {
        // this.tempProduct = item;  = item
        this.tempProduct = Object.assign({}, item); // Object.assign({}, item)item
        this.isNew = false;
      }
      $("#productModal").modal("show");
    },

    // modal
    openDelModal(item) {
      console.log(item);
      this.tempProduct = item;
      $("#delProductModal").modal("show");
    },

    // 
    deleteProduct() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
      console.log(process.env.VUE_APP_APIPATH, process.env.VUE_APP_CUSTOMPATH);
      this.$http.delete(api).then(response => {
        // apithis.$http.delete
        console.log(response.data);
        if (response.data.success) {
          $("#delProductModal").modal("hide");
          vm.getProducts();
        } else {
          $("#delProductModal").modal("hide");
          vm.getProducts();
          console.log("");
        }
      });
    },

    // 
    updateProduct() {
      // API                                          // /products
      // ifapiconst apilet apiconst
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`; // /productadminapi
      let httpMethod = "post";
      const vm = this; // APIdatavmthis

      //  ifapi
      if (!vm.isNew) {
        // isNewapi
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpMethod = "put";
      }
      console.log(process.env.VUE_APP_APIPATH, process.env.VUE_APP_CUSTOMPATH);
      this.$http[httpMethod](api, { data: vm.tempProduct }).then(response => {
        // this.$http.getpost
        // vm.tempProduct
        // data{}data{ data: vm.tempProduct }
        console.log(response.data);
        // vm.products = response.data.products;
        // response.dataAPIdataproducts:[]
        // vm.productsresponse.datavm

        // ifmodal
        if (response.data.success) {
          $("#productModal").modal("hide"); // modal('hide')modal
          vm.getProducts(); // api
        } else {
          // 
          // modalapiconsole
          $("#productModal").modal("hide");
          vm.getProducts();
          console.log("");
        }
      });
    },
    // 
    uploadFile() {
      console.log(this);
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;
      const formData = new FormData();
      formData.append("file-to-upload", uploadedFile); // 'file-to-upload'uploadedFile
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`; // 
      vm.status.fileUploading = true; // loading
      this.$http
        .post(url, formData, {
          // (url=, formData=, FormData
          headers: {
            "Content-Type": "multipart/form-data" // multipart/form-dataFormData
          }
        })
        .then(response => {
          // Promise
          console.log(response.data);
          vm.status.fileUploading = false; // 
          // tempProductimageUrl
          // iftempProduct
          // if (response.data.success) {
          //   vm.tempProduct.imageUrl = response.data.imageUrl;
          // }
          // gettersetter
          // set
          if (response.data.success) {
            vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl); // vm.$set(, , )
          } else {
            this.$bus.$emit("message:push", response.data.message, "danger");
          }
        });
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading; // store/index.jsvuexisLoading
    },
  },
  // getProductsapi
  created() {
    this.getProducts();
  }
};
</script>

<style scoped>
  
th, td {
  text-align: center;
}

</style>