<template>
  <div>
    <Loading :active.sync="isLoading"></Loading>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th>Email</th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in sortOrder" :key="key" :class="{'text-secondary': !item.is_paid}">
          <td>{{ item.create_at | date }}</td>
          <td>{{ item.user.name }}</td>
          <td>{{ item.user.tel }}</td>
          <td>{{ item.user.email }}</td>
          <td>
            <ul class="list-unstyled">
              <li
                v-for="(product, i) in item.products"
                :key="i"
              >{{ product.product.title }} {{ product.qty }}{{ product.product.unit }}</li>
            </ul>
          </td>
          <td class="text-center">{{ item.total | currency }}</td>
          <td>
            <strong v-if="item.is_paid" class="text-success"></strong>
            <span v-else class="text-muted"></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="getOrder(item.id)"></button>
          </td>
        </tr>
      </tbody>
    </table>

    <!-- modal -->
    <div
      class="modal fade"
      id="orderModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-width-ctrl" role="document">
        <div class="modal-content">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>

          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12"></div>
              <div class="form-group col-sm-6">
                <div class="h5 text-dark"></div>
                <div class="h6 text-dark">
                  
                  <span class="text-grey">{{ order.create_at | date }}</span>
                </div>
                <div class="h6 text-dark">
                  
                  <span class="text-grey">{{ order.id }}</span>
                </div>
                <hr />

                <div class="h5 text-dark"></div>
                <ul class="list-unstyling">
                  <li
                    v-for="(product, i) in order.products"
                    :key="i"
                  >{{ product.product.title }} {{ product.qty }}{{ product.unit }}</li>
                </ul>
                <hr />
                <div class="h6 text-dark">
                  
                  <span class="text-grey">{{ order.total | currency }}</span>
                </div>
                <div class="h6 text-dark">
                  
                  <span class="text-success" v-if="order.is_paid"></span>
                  <span class="text-danger" v-else></span>
                </div>
              </div>

              <div class="form-group col-sm-6">
                <div class="h5 text-dark"></div>
                <div class="h6 text-dark">
                  
                  <span class="text-grey">{{ order.user.name }}</span>
                </div>
                <div class="h6 text-dark">
                  
                  <span class="text-grey">{{ order.user.tel }}</span>
                </div>
                <div class="h6 text-dark">
                  
                  <span class="text-grey">{{ order.user.email }}</span>
                </div>
                <div class="h6 text-dark">
                  
                  <span class="text-grey">{{ order.user.address }}</span>
                </div>
                <hr />
                <div class="h5 text-dark"></div>
                <div class="text-grey" v-if="order.message">{{ order.message }}</div>
                <div class="text-muted" v-else>--</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <Pagination :pagination="pagination" @emitPages="getOrders"></Pagination>
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination";

export default {
  name: "Orders",
  data() {
    return {
      orders: [],
      order: {
        user: {
          name: "",
          tel: "",
          email: "",
          address: "",
        },
      },
      isNew: false,
      pagination: {
        total_page: 1,
        current_page: 1,
        has_pre: false,
        has_next: false,
        category: null,
      },
      // isLoading: false,
    };
  },
  components: {
    Pagination,
  },
  methods: {
    getOrders(currentPage = 1) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/orders?page=${currentPage}`;
      vm.$store.dispatch("updateLoading", true);
      this.$http.get(url, vm.tempProduct).then((response) => {
        vm.orders = response.data.orders;
        vm.pagination = response.data.pagination;
        vm.$store.dispatch("updateLoading", false);
        console.log("getOrders", response);
      });
    },
    getOrder(id) {
      // getProduct(id)s
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${id}`;
      // vm.isLoading = true; // fontawesomeisLoading
      // vm.status.loadingItem = id; // status.loadingItem = idhtmliv-if
      this.$http.get(url).then((response) => {
        vm.order = response.data.order;
        $("#orderModal").modal("show"); // ajaxmodal
        console.log("getOrder", response);
        // vm.isLoading = false; // 
        // vm.status.loadingItem = ""; // loadingItem
      });
    },
  },
  computed: {
    sortOrder() {
      const vm = this;
      let newOrder = [];
      if (vm.orders.length) {
        newOrder = vm.orders.sort((a, b) => {
          const aIsPaid = a.is_paid ? 1 : 0;
          const bIsPaid = b.is_paid ? 1 : 0;
          return bIsPaid - aIsPaid;
        });
      }
      return newOrder;
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading; // store/index.jsvuexisLoading
    },
  },
  created() {
    this.getOrders();
    console.log(process.env.VUE_APP_APIPATH);
  },
};
</script>


