<template>
  <nav aria-label="Page navigation example">
    <ul class="pagination">
      <li class="page-item" :class="{'disabled': !pagination.has_pre }">
        <!-- -->
        <a
          class="page-link"
          href="#"
          aria-label="Previous"
          @click.prevent="getOrders(pagination.current_page - 1)"
        >
          <!-- pagination.current_page - 1 -->
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>
      <!-- v-forkeypage -->
      <!-- class -->
      <li
        class="page-item"
        v-for="page in pagination.total_pages"
        :key="page"
        :class="{'active': pagination.current_page === page, }"
      >
        <a class="page-link" href="#" @click.prevent="getOrders(page)">{{ page }}</a>
        <!-- getProductspage -->
      </li>
      <li class="page-item" :class="{'disabled': !pagination.has_next }">
        <!--  -->
        <a
          class="page-link"
          href="#"
          aria-label="Next"
          @click.prevent="getOrders(pagination.current_page + 1)"
        >
          <!-- pagination.current_page + 1 -->
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: 'Pagination',
  props: ['pagination'],
  data () {
    return {}
  },
  methods: {
    getOrders(currentPage = 1) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/orders?page=${currentPage}`;
      vm.$store.dispatch("updateLoading", true);
      this.$http.get(url, vm.tempProduct).then((response) => {
        vm.orders = response.data.orders;
        vm.pagination = response.data.pagination;
        vm.$store.dispatch("updateLoading", false);
        console.log(response);
      });
    },
    newPage(page){
      this.$emit('emitPage',page)
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
  },
}
</script>