<template>
      <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt />
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">NT:$ {{ product.origin_price }} </div>
              <del class="h6" v-if="product.price">NT:$ {{ product.origin_price }} </del>
              <div class="h4" v-if="product.price">NT:$ {{ product.price }} </div>
            </div>
            <select name class="form-control mt-3" v-model="product.num">
              <!-- v-for="num in 10"10 -->
              <option :value="num" v-for="num in 10" :key="num"> {{num}} {{product.unit}}</option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
              
              <strong>{{ product.num * product.price }}</strong> 
            </div>
            <button
              type="button"
              class="btn btn-primary"
              @click="addtoCart(product.id, product.num)"
            >
              <!-- product.iddataproduct{}product -->
              <!-- <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === product.id"></i> -->
              
            </button>
          </div>
        </div>
  <!-- </div> -->
</template>


<script>
import $ from "jquery";
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      product: {},
      // isLoading: false,
    };
  },
  methods: {
    getProduct(id) {
      // getProduct(id)s
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      // vm.isLoading = true; // fontawesomeisLoading
      // vm.status.loadingItem = id; // status.loadingItem = idhtmliv-if
      this.$http.get(url).then((response) => {
        vm.product = response.data.product;
        $("#productModal").modal("show"); // ajaxmodal
        console.log("getProduct", response);
        // vm.isLoading = false; // 
        // vm.status.loadingItem = ""; // loadingItem
      });
    },
  },
  computed: {
    ...mapGetters(['isLoading']),
  },
  created() {
    this.getProduct(id);
    console.log(this.$route.params.id);
    this.$route.params.id;
  },
};
</script>