<template>
  <div class="my-5 row justify-content-center">
    <form class="col-md-6" @submit.prevent="payOrder">
      <table class="table col-sm-12">
        <thead>
          <th></th>
          <th></th>
          <th></th>
        </thead>
        <tbody>
          <tr v-for="item in order.products" :key="item.id">
            <td class="align-middle">{{ item.product.title }}</td>
            <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
            <td class="align-middle text-right">{{ item.final_total }}</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="2" class="text-right"></td>
            <td class="text-right">{{ order.total }}</td>
          </tr>
        </tfoot>
      </table>

      <table class="table col-sm-12">
        <tbody>
          <tr>
            <th width="100">Email</th>
            <td>{{ order.user.email }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.name }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.tel }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.address }}</td>
          </tr>
          <tr>
            <th></th>
            <td>
              <span v-if="!order.is_paid"></span>
              <span v-else class="text-success"></span>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="text-right" v-if="order.is_paid === false">
        <button class="btn btn-danger"></button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    // 
    return {
      order: {
        user: {
          address: "",
          email: "",
          tel: "",
          name: ""
        }
      },
      orderId: "" // 
    };
  },
  methods: {
    getOrder() {
      // 
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${vm.orderId}`; // 
      vm.$store.dispatch("updateLoading", true);
      this.$http.get(url).then(response => {
        vm.order = response.data.order;
        console.log(response);
        vm.$store.dispatch("updateLoading", false);
      });
    },
    payOrder() {
      // 
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${vm.orderId}`; // api
      vm.$store.dispatch("updateLoading", true);
      this.$http.post(url).then(response => { // ajaxpost
        // vm.order = response.data.order; // 
        console.log(response);
        if(response.data.success) {
            alert('')
            vm.getOrder();
            vm.$router.push(`/product_list`);
        }
        vm.$store.dispatch("updateLoading", false);
      });
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
  },
  created() {
    // this.orderId = this.$route.params.orderId; // $route.paramsparamsorderId
    console.log(this.orderId); // 
    this.getOrder();
  }
};
</script>
